.table-wrapper {
      overflow-x: auto;
    }
    
    .custom-table {
      width: 100%;
      border-collapse: collapse;
    }

    .custom-header {
      background-color: #f97316;
     color: white;
      padding: 10px;
      text-align: center;
    }
    
    .custom-row:nth-child(odd) {
      background-color: #f2f2f2;
    }
    
    .custom-row:hover {
      background-color: #ddd;
    }
    
    .custom-cell {
      padding: 8px;
  border: 1px solid #ddd;
  text-align: center;
    }